div#col1 {
  display: flex;
  flex-direction: column;

  > * + * {
    margin-top: $gap;
  }

  > div:first-child {
    @extend %not-select;
  }
}

#stats-table {
  > div {
    display: grid;
    grid-template-columns: 1fr repeat(3, 23%);
    justify-items: center;

    &.header,
    &.footer {
      border-top: medium solid $blue0;
      border-bottom: medium solid $blue0;
    }

    &.body {
      @extend %transition-collapse;
    }

    &.footer.collapse {
      border-top: none;
    }

    > span {
      padding: 4px 0;
    }
  }
}

#damage-bar {
  @extend %center;
  height: 56px;
  border-radius: 8px;
  color: white;
  font-weight: bold;
}

table {
  width: 100%;
  table-layout: fixed;

  td {
    text-align: center;
  }
}
