img,
input[type='image'] {
  @extend %not-select;

  &.xs {
    width: 24px;
    height: 24px;
  }

  &.sm {
    width: 32px;
    height: 32px;
  }

  &.md {
    width: 64px;
    height: 64px;
  }

  &.lg {
    width: 80px;
    height: 80px;
  }
}

input[type='image'] {
  @extend %shadow;

  &.lg {
    outline: none;
  }

  &:active {
    box-shadow: none;
  }
}

@mixin input-image-hover {
  .scale {
    transform: scale(1.1);
  }

  input[type='image'] {
    &.md {
      outline-color: $blue0;

      &:hover {
        transform: scale(1.1);
      }
    }
  }
}
